import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import io from "socket.io-client";

Vue.use(Vuex);
var api = "https://backend.tizi.vn";
var token = localStorage.token ? localStorage.token : false;
var socket = io(api);

function sort(data, parent_id, str = "", disabled = false) {
    var parent = [];
    for (var item of data) {
        if (item.parent_id == parent_id) {
            var result = sort(data, item.id, str + '-----|', disabled);
            if (!disabled) parent.push({ id: item.url, text: str + item.name });
            else parent.push({ id: item.id, text: str + item.name, disabled: result.length });
            parent = parent.concat(result);
        }
    }
    return parent;
}

export default new Vuex.Store({
    state: {
        domainApi: api,
        socket: socket,
        route: false,
        token: token,
        user: {},
        boxMessage: {
            isBoxMessage: false,
            data: {},
            content: []
        },
        isMessage: false,
        countNewMessage: 0,
        isNotification: false,
        countNewNotification: 0,
        countProductFollow: 0,
        online: false,
        users_online: [],
        province: [],
        category: [],
        pageSize: 10,
        pageCount: 0,
        admin: {
            isOpen: localStorage.isOpen ? JSON.parse(localStorage.isOpen) : {},
            product: {
                index: 0,
                actived: 0,
                success: 0,
                block: 0,
                feedback: 0
            },
            user: {
                index: 0
            }
        }
    },
    getters: {
        socket: state => state.socket,
        countProduct: state => state.admin.product,
        getCategoryParent: state => state.category.filter(e => e.parent_id == 0),
        getProvince: state => state.province,
        getCategoryByUrl: state => url => {
            if (url == 'tat-ca-danh-muc') return { name: "Tất cả danh mục", title: "Tất cả danh mục" };
            return state.category.find(e => e.url == url)
        },
        getCategorySort: state => disabled => {
            if (disabled == false) return [{ id: 'tat-ca-danh-muc', text: "Tất cả danh mục" }].concat(sort(state.category, 0, '', disabled));
            else return [{ id: 'chon-chuyen-muc', text: "---Chọn chuyên mục---" }].concat(sort(state.category, 0, '', disabled));
        },
        getProvinceByUrl: state => url => {
            if (url == 'toan-quoc') return { name: "Toàn quốc", title: "Toàn quốc" };
            if (url == 'tat-ca-quan-huyen') return { name: "Tất cả quận huyện", title: "Tất cả quận huyện" };
            return state.province.find(e => e.url == url)
        },
        getProvinceByLv: state => (lv, item, isUrl = false) => {
            return state.province.filter(e => e.parent_id == lv).reduce((result, obj) => {
                var object = {
                    id: isUrl ? obj.url : obj.id,
                    text: obj.name
                }
                result.push(object);
                return result;
            }, [item])
        },
        getUser: state => {
            return state.user;
        },
        getCategories: state => state.category,
        countProductFollow: state => state.countProductFollow,
        getProductFollow: state => state.user.productFollow,
        statusBoxMessage: state => state.boxMessage.isBoxMessage,
        boxMessageData: state => state.boxMessage.data,
        boxMessageContent: state => state.boxMessage.content,
        countNewMessage: state => state.countNewMessage,
        isMessage: state => state.isMessage,
        countNewNotification: state => state.countNewNotification,
        isNotification: state => state.isNotification,
        online: state => state.online,
        users_online: state => state.users_online
    },
    mutations: {
        setData(state, response) {
            if (response[0].data.success != 0) {
                var user = response[0].data.result;
                // ket noi socket
                state.socket.emit("user_connect", user);

                // so tin da luu cua user
                axios.get(`${api}/public/product-follow/${response[0].data.result.id}`)
                    .then(res => {
                        user.productFollow = res.data;
                        state.countProductFollow = res.data.length;
                        state.user = user;
                    });

                axios.get(`${api}/auth/message/count-new-message`, { headers: { Authorization: `Bearer ${token}` } })
                    .then(res => {
                        state.countNewMessage = res.data.count_message;
                    });

                axios.get(`${api}/auth/notification/count-new-notification`, { headers: { Authorization: `Bearer ${token}` } })
                    .then(res => {
                        state.countNewNotification = res.data.count_notification;
                    });
            } else {
                state.token = false;
                state.user = {};
                state.socket.emit("user_connect", null);
            }
            state.province = response[1].data;
            state.category = response[2].data;
        },
        setDataAfterLogin(state, id_user) {
            axios.get(`${api}/public/product-follow/${id_user}`)
                .then(res => {
                    state.user.productFollow = res.data;
                    state.countProductFollow += res.data.length;
                    // ket noi socket
                    state.socket.emit("user_connect", state.user);
                });

            axios.get(`${api}/auth/message/count-new-message`, { headers: { Authorization: `Bearer ${token}` } })
                .then(res => {
                    state.countNewMessage += res.data.count_message;
                });
        },
        setCountProduct(state, response) {
            state.admin.product.index = Number(response[0].data.count);
            state.admin.product.actived = Number(response[1].data.count);
            state.admin.product.success = Number(response[2].data.count);
            state.admin.product.block = Number(response[3].data.count);
            state.admin.product.feedback = Number(response[4].data.count);
        },
        setCountUser(state, response) {
            state.admin.user.index = Number(response[0].data.count_user);
        },
        getProvinceParent(state, id) {
            return state.province.filter(e => e.parent_id == id);
        },
        getRoute(state, route) {
            state.route = {
                name: route.name,
                params: route.params,
                query: route.query
            }
        }
    },
    actions: {
        async loadData({ commit }) {
            var response = await Promise.all([
                axios.get(`${api}/auth`, { headers: { Authorization: `Bearer ${token}` } }),
                axios.get(`${api}/public/province`),
                axios.get(`${api}/public/category`)
            ]);
            commit('setData', response);
        },
        async loadDataProductAdmin({ commit }) {
            token = localStorage.token ? localStorage.token : false;
            var responseProduct = await Promise.all([
                axios.get(api + "/admin/product", { headers: { Authorization: `Bearer ${token}` }, params: { status: 0 } }),
                axios.get(api + "/admin/product", { headers: { Authorization: `Bearer ${token}` }, params: { status: 1 } }),
                axios.get(api + "/admin/product", { headers: { Authorization: `Bearer ${token}` }, params: { status: 2 } }),
                axios.get(api + "/admin/product", { headers: { Authorization: `Bearer ${token}` }, params: { status: 3 } }),
                axios.get(api + "/admin/product", { headers: { Authorization: `Bearer ${token}` }, params: { status: 1, feedback: true } }),
                axios.get(api + "/admin/user/count-all", { headers: { Authorization: `Bearer ${token}` } })
            ]);

            var responseUser = await Promise.all([
                axios.get(api + "/admin/user/count-all", { headers: { Authorization: `Bearer ${token}` } })
            ]);
            commit('setCountProduct', responseProduct);
            commit('setCountUser', responseUser);
        }
    }
});
